.banner{
    
    background-size: cover;
    height: 449px;
    color:aliceblue;
    
}
.content{
    position: relative;
    padding-top: 140px;
    height: 190px;
    padding-left: 15px;
}
.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.button{
    color:white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51,51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}

button:hover{
    background-color:black;
    color:aliceblue
}
.description{
    width: 45rem;
    height: 80px;
    font-size: 1.2rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: 0.9rem;
    max-width: 360px;
}
.fade_bottom{

    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent, rgba(37, 37, 37, 0.61),#111);
}